(function() {
   'use strict';
    
    Dom.prototype.fadeIn = function(duration, callback) {
        var elements = [];

        this.elements.forEach(function(element) {
          fade(element, duration, 'in', callback);
        });

        return new Dom(elements);
    }
    
    Dom.prototype.fadeOut = function(duration, callback) {
        var elements = [];

        this.elements.forEach(function(element) {
          fade(element, duration, 'out', callback);
        });

        return new Dom(elements);
    }
    
    function fade(element, duration, direction, callback) {
        var style = element.style,
          frameRate = 10,
          opacity,
          totalFrames,
          second = 1000,
          duration = duration || 400,
          interval = second / frameRate,
          elementOpacity = element.opacity,
          finalOpacity,
          opacityIncrement;

        if (duration < 10) {
          duration = 10;
        }

        if (typeof duration === 'string') {

          if(duration === 'in') {
            duration = 600;
          } else if (duration === 'fast') {
            duration = 200;
          } else {
            duration = 400;
          }
        }

        totalFrames = duration / frameRate;

        if (direction === 'in') {
          if (elementOpacity !== 0) {
            return;
          }
          opacity = 0;
          finalOpacity = style.height.split('px')[0];
          style.display = 'block';
          style.opacity = '0';
          opacityIncrement = finalOpacity / totalFrames;
        } else {
          if (elementOpacity === 0) {
            return;
          }
          opacity = elementOpacity;
          finalOpacity = 0;
          opacityIncrement = opacity / totalFrames;
        }

        var tween = function() {
          if (direction === 'down') {
            opacity += opacityIncrement;
            style.opacity = opacity;
            if (opacity < finalOpacity) {
              setTimeout(tween, interval);
            }
          } else {
            opacity -= opacityIncrement;
            style.opacity = opacity;
            if (opacity > finalOpacity) {
              setTimeout(tween, interval);
            } else {
              style.display = 'none';
              style.opacity = elementOpacity;
            }
          }
        };

    tween();

    if(callback) {
      callback();
    }
  }
})();